// utility to check a type
export var isNil = function (o) {
    if (o === undefined || o === null)
        return true;
    return false;
};
export var isString = function (o) {
    if (isNil(o))
        return false;
    if (typeof o === "string")
        return true;
    return false;
};
export var isNumber = function (o) {
    if (isNil(o))
        return false;
    if (typeof o === "number")
        return true;
    return false;
};
export var isDate = function (o) {
    if (isNil(o))
        return false;
    if (o instanceof Date)
        return true;
    return false;
};
export var isBoolean = function (o) {
    if (isNil(o))
        return false;
    if (typeof o === "boolean")
        return true;
    return false;
};
export var isArray = function (o) {
    if (isNil(o))
        return false;
    if (o instanceof Array)
        return true;
    return false;
};
export var isObject = function (o) {
    if (isNil(o))
        return false;
    if (o instanceof Object)
        return true;
    return false;
};
