import { isNil, isString } from "../utils/typeChecker";
export var validateString = function (o, schema, fieldPath) {
    //
    // isnil
    if (isNil(o)) {
        if (!schema.optional) {
            return {
                isValid: false,
                errorMessage: "Required field.",
                fieldPath: fieldPath
            };
        }
        return {
            isValid: true,
            errorMessage: "",
            fieldPath: fieldPath
        };
    }
    if (!isString(o)) {
        return {
            isValid: false,
            errorMessage: "Should be a string.",
            fieldPath: fieldPath
        };
    }
    var str = o;
    // min length
    if (!isNil(schema.minLength) && str.length < schema.minLength) {
        return {
            isValid: false,
            errorMessage: "Should be at least " + schema.minLength + " characters.",
            fieldPath: fieldPath
        };
    }
    // max length
    if (!isNil(schema.maxLength) && str.length > schema.maxLength) {
        return {
            isValid: false,
            errorMessage: "Should not be longer than " + schema.maxLength + " characters.",
            fieldPath: fieldPath
        };
    }
    // pattern
    if (!isNil(schema.regex)) {
        var match = schema.regex.test(o);
        if (!match) {
            return {
                isValid: false,
                errorMessage: "Should match the pattern " + schema.regex + " .",
                fieldPath: fieldPath
            };
        }
    }
    return {
        isValid: true,
        errorMessage: "",
        fieldPath: fieldPath
    };
};
