import { isBoolean, isNil } from "../utils/typeChecker";
export var validateBoolean = function (o, schema, fieldPath) {
    //
    // isnil
    if (isNil(o)) {
        if (!schema.optional) {
            return {
                isValid: false,
                errorMessage: "Required field.",
                fieldPath: fieldPath
            };
        }
        return {
            isValid: true,
            errorMessage: "",
            fieldPath: fieldPath
        };
    }
    if (!isBoolean(o)) {
        return {
            isValid: false,
            errorMessage: "Should be true or false.",
            fieldPath: fieldPath
        };
    }
    return {
        isValid: true,
        errorMessage: "",
        fieldPath: fieldPath
    };
};
