import { isNil, isNumber } from "../utils/typeChecker";
export var validateNumber = function (o, schema, fieldPath) {
    //
    // isnil
    if (isNil(o)) {
        if (!schema.optional) {
            return {
                isValid: false,
                errorMessage: "Required field.",
                fieldPath: fieldPath
            };
        }
        return {
            isValid: true,
            errorMessage: "",
            fieldPath: fieldPath
        };
    }
    if (!isNumber(o)) {
        return {
            isValid: false,
            errorMessage: "Should be a number.",
            fieldPath: fieldPath
        };
    }
    var numbr = o;
    // min length
    if (!isNil(schema.min) && numbr < schema.min) {
        return {
            isValid: false,
            errorMessage: "Should not be less than " + schema.min + ".",
            fieldPath: fieldPath
        };
    }
    // max length
    if (!isNil(schema.max) && numbr > schema.max) {
        return {
            isValid: false,
            errorMessage: "Should not be larger than " + schema.max + ".",
            fieldPath: fieldPath
        };
    }
    return {
        isValid: true,
        errorMessage: "",
        fieldPath: fieldPath
    };
};
