import { isNil } from "../utils/typeChecker";
import { validate } from "./validate";
export var validateArray = function (arr, schema, fieldPath) {
    //
    // isnil
    if (isNil(arr)) {
        if (!schema.optional) {
            return {
                isValid: false,
                errorMessage: "Required field.",
                items: [],
                fieldPath: fieldPath
            };
        }
        return {
            isValid: true,
            errorMessage: "",
            items: [],
            fieldPath: fieldPath
        };
    }
    var result = {
        isValid: true,
        errorMessage: "",
        items: [],
        fieldPath: fieldPath
    };
    // array min-items
    if (!isNil(schema.minItems) && arr.length < schema.minItems) {
        result.isValid = false;
        result.errorMessage = "Should have at least " + schema.minItems + " items.";
    }
    // array max-items
    if (!isNil(schema.maxItems) && arr.length >= schema.maxItems) {
        result.isValid = false;
        result.errorMessage = "Should not have more than " + (schema.maxItems - 1) + " items.";
    }
    // for each key, validate
    for (var i = 0; i < arr.length; i++) {
        result.items[i] = validate(arr[i], schema.items, fieldPath.concat([i]));
    }
    return result;
};
